























































































import shoppublic from '@/common/shoppublic'; //服务器地址
export default {
	data() {
		return {
			tabsState:'',//选项卡状态
			tabsMaskState:'',//选项卡遮罩显示隐藏
			swiperIndex:'',
			pagesize: 0,
			index: 7,
			pageInfo:[],
		};
	},
	onLoad() {
		this.getPartnerList()
	},
	onReachBottom() {
		this.getPartnerList('onReach');
	},
	methods:{
		// 获取新铺租售列表
		getPartnerList(page = '0') {
			var _this = this;
			var oldVisitorList = _this.pageInfo;
			if(page === 'onReach'){
				uni.request({
					url:shoppublic.getUrl() + 'newShopRent/findlistnewShopRent',
					data:{
						index:_this.index,
						city:610100,
						county:'',
						partner:'',
						type:'',
						statuslist:'状态',// 经营状态
						minarea: '',
						maxarea: '',
						minloadingfee: '',
						maxloadingfee: '',
						
					},
					success: (res) => {
						console.log(res);
						if(page === 'onReach' && res.data.responseBody){
							oldVisitorList = oldVisitorList.concat(res.data.responseBody)
							_this.index += 7;
						}else{
							return false;
						}
						_this.pageInfo = oldVisitorList
					}
				})
			}else{
				uni.request({
					url:shoppublic.getUrl() + 'newShopRent/findlistnewShopRent',
					data:{
						index:_this.pagesize,
						city:610100,
						county:'',
						partner:'',
						type:'',
						statuslist:'状态',// 经营状态
						minarea: '',
						maxarea: '',
						minloadingfee: '',
						maxloadingfee: '',
					},
					success: (res) => {
						console.log(res);
						_this.pageInfo = res.data.responseBody;
					}
				})
			}
		},
		// 区域/费用选择器按钮点击事件
		clickScrollCell(e){
			this.closeTabsMask();
		},
		// 顶部选项卡点击事件
		tabsChanges(state){
			this.tabsState = state;
			this.swiperIndex = state;
			this.tabsMaskState = 'true';
		},
		// 选项卡滑块切换
		swiperChangs(e){
			this.tabsState = e.detail.current;
			this.swiperIndex = e.detail.current;
		},
		// 关闭遮罩
		closeTabsMask(){
			this.tabsState = '';
			this.tabsMaskState = '';
			this.swiperIndex = '';
		},
		// 页面跳转至新铺招租发布页
		toLocationRelease(){
			uni.navigateTo({
				url:"./newShopRentalRelease/newShopRentalRelease"
			})
		},
		// 页面跳转至新铺招租详情页
		toNewStoreRentalDateils(id){
			uni.navigateTo({
				url:"./newStoreRentalDateils/newStoreRentalDateils?id=" + id
			})
		}
	}
}
